import { useEffect, useState, useRef } from 'react';
import logo from './logo.svg';
import './style.sass';
import { text } from './text.js'

function App() {
  const [page, setPage] = useState(0)
  const [content, setContent] = useState([[]])
  const [mouseDown, setMouseDown] = useState(false)
  const [translate, setTranslate] = useState(false)
  const [spans, setSpans] = useState([])
  const [phrase, setPhrase] = useState("")
  const [translation, setTranslation] = useState("")
  const pRef = useRef(null);
  const [scrolledDist, setScrolledDist] = useState(0)
  const [touch, setTouch] = useState(false)
  const [fill, setFill] = useState(false)
  const [spanNumber, setSpanNumber] = useState(0)
  const [scrollUp, setScrollUp] = useState(false)
  const [scrollBehavior, setScrollBehavior] = useState("smooth")
  const [counter, setCounter] = useState(0)


  const [textPages, setTextPages] = useState(text.replace(/(\r\n|\n|\r)/gm, "").match(eval("/.{1,5000}/g")))

  useEffect(() => {


  }, [page])


  useEffect(() => {
    console.log(1010);
    let wordArray = text.replace(/(\r\n|\n|\r)/gm, " ").split(" ")
    let pages = [[]]
    let pageCounter = 0
    for (let i = 0; i < wordArray.length; i++) {
      if (pRef.current.scrollHeight <= pRef.current.clientHeight) {
        pages[pageCounter].push(wordArray[i])
        pRef.current.innerHTML = pages[pageCounter].join(" ")
      } else {
        pages[pageCounter].pop()
        pRef.current.innerHTML = pages[pageCounter].join(" ")
        pRef.current.innerHTML = ""
        // break
        pageCounter++
        pages[pageCounter] = [wordArray[i-1]]
        pages[pageCounter].push(wordArray[i])
      }
    }
    console.log(pages);
    pRef.current.innerHTML = ""
    setContent(pages)
    
  }, [])
  
  useEffect(() => {
    console.log(document.getElementsByTagName("pre").length);
    setTimeout(() => {
      for (let e of document.getElementsByTagName("pre")) {
        if (e.offsetLeft == 0) {
          e.innerHTML = e.innerHTML.replace(/\s/g, '')
        }
      }

    }, 1000)

  }, [content])

  function handleMouse(event, i) {
    // console.log(document.elementFromPoint(event.touches[0].clientX, event.touches[0].clientY));
    let nextSpans = [...spans]
    if (mouseDown) {
      // check mobile or desktop
      // mobile
      if (touch) {
        console.log(event.type);
        if (event.touches) {

          let spanNumber = parseInt(document.elementFromPoint(event.touches[0].clientX, event.touches[0].clientY).getAttribute("data-n"))
          if (!nextSpans.includes(spanNumber)) {
            nextSpans.push(spanNumber)

          } else {
            if (nextSpans.length > 1) {
              let firstElement = Math.min(...nextSpans)
              let lastElement = spanNumber > firstElement ? spanNumber : Math.max(...nextSpans)
              nextSpans = []
              for (let span = firstElement; span <= lastElement; span++) {
                nextSpans.push(span)
              }
            }
          }
          console.log(nextSpans.map(el => content[el]).join(" "));

          let req = 'https://translate-service.scratch.mit.edu/translate?language=ru&text=' + nextSpans.map(el => content[el]).join(" ")
          console.log(req);

        }
      }
      // desktop
      else {
        if (!nextSpans.includes(i)) {
          nextSpans.push(i)

        } else {
          if (nextSpans.length > 1) {
            let firstElement = Math.min(...nextSpans)
            let lastElement = i > firstElement ? i : Math.max(...nextSpans)
            nextSpans = []
            for (let span = firstElement; span <= lastElement; span++) {
              nextSpans.push(span)
            }
          }
        }
      }
      console.log(nextSpans);
      setSpans(nextSpans)
      setPhrase(nextSpans.map(el => content[page][el]).join(" "))
    }
  }



  useEffect(() => {
    // console.log(phrase);
    // console.log(pRef.current.clientHeight, pRef.current.scrollHeight, pRef.current);
    setTranslation("...")
  }, [phrase])





  function handleTranslate() {
    fetch("https://translate-service.scratch.mit.edu/translate?language=ru&text=" + phrase)
      .then(response => response.json())
      .then(commits => {
        console.log(phrase, commits.result);
        setTranslation(commits.result)
      });
  }


  function handleScroll(up) {
    let scrollNumber = pRef.current.clientHeight
    let nextDist = up ? scrolledDist <= 0 ? 0 : (scrolledDist - scrollNumber) : ((pRef.current.scrollHeight - scrolledDist) > scrollNumber) ? (scrolledDist + scrollNumber) : scrolledDist;

    // if (up){
    //   if (scrolledDist <= 0) {
    //     nextDist = 0
    //   } else {
    //     nextDist = scrolledDist - scrollNumber
    //   }
    // } else {
    //   if () {

    //   }
    // }

    // console.log(nextDist, scrolledDist);
    pRef.current.scrollTo({
      top: nextDist,
      left: 0,
      behavior: scrollBehavior,
    })
    console.log(up, scrolledDist, nextDist, pRef.current.scrollHeight);
    if (nextDist === scrolledDist) {
      if (nextDist == 0) {
        handleFlip(true)
      } else if ((textPages.length - 1) > page) {
        handleFlip(false)
      }
    }
    else {
      setScrolledDist(nextDist)

    }
  }

  function handleFlip(up) {
    console.log(12);
    let nextPage = up ? page > 0 ? (page - 1) : 0 : page < (textPages.length - 1) ? page + 1 : page
    setPage(nextPage)
    if (up) {
      setScrollUp(true)
    } else {
      setScrollUp(false)

      pRef.current.scrollTo({
        top: 0,
        left: 0,
        behavior: scrollBehavior,
      })
      setScrolledDist(0)
    }
  }

  return (
    <div className="App">
      <h1>Kirill's</h1>
      <div id='mainText' ref={pRef} onMouseDown={() => { setMouseDown(true); setSpans([]); }} onMouseUp={() => { setMouseDown(false); handleTranslate() }} onTouchStart={() => { setMouseDown(true); setSpans([]); setTouch(true) }} onTouchEnd={() => { setMouseDown(false); handleTranslate() }}>
        {
          
          content[page].map((el, i) =>
            <pre key={i} data-n={i} onDoubleClick={() => setTranslate(t => !t)} onClick={() => { setSpans([i]); setPhrase(content[i]) }} onMouseMove={(event) => handleMouse(event, i)} onTouchMove={(event) => handleMouse(event, i)} style={spans.includes(i) ? { backgroundColor: "#ffffe0", touchAction: "none" } : translate ? { color: "#666", touchAction: "none" } : {}}> {el}</pre>
          )
        }
      </div>
      <form action="" className="buttons" style={translate ? { opacity: 1, pointerEvents: "auto" } : {}}>
        <button type='button' onClick={() => handleScroll(true)}>up</button>
        <h6>page {page + 1}</h6>
        <button type='button' onClick={() => setPage(p => p + 1)}>down</button>
        <p>{translation}</p>
      </form>
    </div>
  );
}

export default App;
